  font-family: 'Comfortaa';
  font-style: normal;
  font-weight: 400;
  src: local(''),
  url('../../static/fonts/comfortaa-v28-greek_latin-ext_vietnamese_cyrillic-ext-regular.woff2') format('woff2'),
